.HousingBanner{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  max-width: 1240px;
  height: 415px;
  background-color: rgba(97, 94, 94, 0.651);
  border-radius: 25px;
  margin: auto auto 45px auto;
  overflow: hidden;
}

.HousingBanner-img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}